import Typography from 'components/Typography';
import Page from 'components/Page';
import React, {useState} from 'react';
import '../styles/pre.scss'


import {
  Card, CardBody, CardHeader, Col, Row
} from 'reactstrap';

function WalletPage()
{

  const [userhash_1, setUserHash_1] = useState('');
  const onChange_1 = (e) => {
    setUserHash_1(e.target.value);
  };

  const [userhash_2, setUserHash_2] = useState('');
  const onChange_2 = (e) => {
    setUserHash_2(e.target.value);
  };

  return (
    <Page title="Wallet API">
      <Row>
        <Col>
          <Card>
            <CardHeader>설명</CardHeader>
            <CardBody>
              <Typography>
                고객 지갑과 관련된 Wallet API를 제공합니다. <br/>
              </Typography>
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col>
          <Card>
            <CardHeader>Wallet 개설 API</CardHeader>
            <CardBody>
              <Typography >
                고객 지갑 발행을 위한 API<br/>
                POST방식 : https://nft-api-dev.shinsegae-inc.com/event/wallet/wallet-address?user_hash= <p className='valuepre'>{userhash_1}</p>
              </Typography>
              
              userhash : <input onChange={onChange_1} value={userhash_1}/> 
                 
              <pre className='precode'>
                async function walletGenerate(userHash)<br/>
                &#123;<br/>
                &nbsp;const options = <br/>
                &nbsp;&#123; <br/>
                &nbsp;&nbsp;&nbsp;hostname: 'nft-api-dev.shinsegae-inc.com', <br/>
                &nbsp;&nbsp;&nbsp;port: 443, <br/>
                &nbsp;&nbsp;&nbsp;path: '/wallet?user_hash=' + <p className='valuepre'>{userhash_1}</p>, <br/>
                &nbsp;&nbsp;&nbsp;method: ''POST', <br/>
                &nbsp;&nbsp;&nbsp;headers: &#123; <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'Content-Type': 'application/json', <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'Authorization': authModel.getStoredToken(), <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;, <br/>
                &nbsp;&#125; <br/>
                &nbsp;return await commonModel.httpRequest(options, "") <br/>
                &#125;
              </pre>
            </CardBody>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>Wallet 조회 API</CardHeader>
            <CardBody>
            <Typography>
                고객 지갑 발행을 위한 API<br/>
                GET방식 : https://nft-api-dev.shinsegae-inc.com/event/wallet/wallet-address?user_hash= <p className='valuepre'>{userhash_2}</p>              
            </Typography>

              userhash : <input onChange={onChange_2} value={userhash_2}/> 
            
              <pre className='precode'>
                async function getWalletInfo(userHash)<br/>
                &#123;<br/>
                &nbsp;const options = <br/>
                &nbsp;&#123; <br/>
                &nbsp;&nbsp;&nbsp;hostname: 'nft-api-dev.shinsegae-inc.com', <br/>
                &nbsp;&nbsp;&nbsp;port: 443, <br/>
                &nbsp;&nbsp;&nbsp;path: '/wallet?user_hash=' + <p className='valuepre'>{userhash_2}</p>, <br/>
                &nbsp;&nbsp;&nbsp;method: 'GET', <br/>
                &nbsp;&nbsp;&nbsp;headers: &#123; <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'Content-Type': 'application/json', <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'Authorization': authModel.getStoredToken(), <br/>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;, <br/>
                &nbsp;&#125; <br/>
                &nbsp;return await commonModel.httpRequest(options, "") <br/>
                &#125;
              </pre>
              
            </CardBody>
          </Card>
        </Col>
      </Row>

    </Page>
  )
};

export default WalletPage;
